import React, { Component } from 'react';

class Event extends Component {
  state = {
    expanded: false,
  }

  onDetailsButtonClicked = () => {
    this.setState(prevState => ({
      expanded: !prevState.expanded
    }));
  }

  render() {
    return (
      <li className="Event">
        <p className="time">{this.props.event.local_time} - {this.props.event.local_date}</p>
        <p className="name">{this.props.event.name}</p>
        <p className="group-name">Group: {this.props.event.group.name}</p>
        <p className="going">{this.props.event.yes_rsvp_count} people are going</p>
        {this.state.expanded &&
          <div className="extra">
            <p className="address">
              {this.props.event.venue.name
                + ', ' + this.props.event.venue.address_1
                + ', ' + this.props.event.venue.city
                + ', ' + this.props.event.venue.localized_country_name
              }
            </p>
            <div className="description" dangerouslySetInnerHTML={{__html: this.props.event.description}} />
            <a className="link" href={this.props.event.link}>Event Link</a>
          </div>
        }
        <button className="details-btn" onClick={this.onDetailsButtonClicked}>Details</button>
      </li>
    );
  }
}

export default Event;