import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import CitySearch from './CitySearch';
import TimeFilter from './TimeFilter';
import EventList from './EventList';
import { getUpcomingEvents } from './api';

class App extends Component {
  state = {
    events: [],
    lat: null,
    lon: null,
    radius: null,
  }

  componentDidMount() {
    this.updateEvents();
  }

  updateCity(lat, lon) {
    this.setState({ lat, lon });
    this.updateEvents();
  }

  updateEvents = () => {
    getUpcomingEvents(this.state.lat, this.state.lon, this.state.radius).then(info => this.setState({ events: info.events }));
  }

  render() {
    return (
      <div className="App">
        <h1>Meetup Events</h1>
        <CitySearch updateCity={this.updateCity}/>
        <TimeFilter />
        <EventList events={this.state.events} />
      </div>
    );
  }
}

export default App;
